<template>
    <div class="all_com">
        <div class="title_com">用户详情</div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>用户信息</span>
            </div>
            <table class="usermessage">
                <tr>
                    <td>ID</td>
                    <td>{{Id}}</td>
                </tr>
                <tr>
                    <td>用户名</td>
                    <td>{{Account}}</td>
                </tr>
                <tr>
                    <td>邮箱</td>
                    <td>
                        <span v-if="1">
                            <span v-if='EmailVerified' class="tagPresell">{{Email}} 已验证</span>
                            <span v-else>
                                未验证
                                <el-button type="primary" size="mini" @click="vertifyEmail">发送验证邮件</el-button><!--
                                --><el-button type="primary" size="mini" @click="dialogVisible = true">修改</el-button>
                            </span>
                        </span>
                        <span v-else>未绑定</span>
                    </td>
                </tr>
                <tr>
                    <td>状态</td>
                    <td>
                        <el-button size="mini" @click="updateUserActive(true)" :class="this.userStatus?'':'activeCls'">正常</el-button>
                        <el-button size="mini" @click="updateUserActive(false)" :class="this.userStatus?'activeCls':''">封禁</el-button>
                    </td>
                </tr>
                <tr>
                    <td>注册时间</td>
                    <td>{{RegistTime}}</td>
                </tr>
                <tr>
                    <td>注册IP</td>
                    <td>{{RegistIp}}</td>
                </tr>
                <tr>
                    <td>注册来源</td>
                    <td>{{Source}}</td>
                </tr>
                <tr>
                    <td>资金密码</td>
                    <td>
                        <span v-if='this.LoginPswdHash'>已设置</span>
                        <span v-else>
                            未设置
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>谷歌验证</td>
                    <td>
                        <span v-if='this.totp'>已设置 
                            <el-button style="margin-left:20px" size="mini" @click="unbundle()">解绑</el-button>
                        </span>
                        <span v-else>
                            未设置
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>短信验证</td>
                    <td>
                        <!-- <el-button type="primary" size="mini" @click="rebuidSms">重置</el-button> -->
                        <span v-if='this.sms'>{{PhonePrefix}}-{{PhoneNumber}}<span style="margin-left:10px">已设置</span></span>
                        <span v-else>
                            未设置
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>BTC余额</td>
                    <td>{{Btc}}</td>
                </tr>
            </table>
        </el-card>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>购买记录</span>
            </div>
            <el-table
                    :data="tableData1"
                    border
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="CreateTime"
                        label="时间">
                    <template slot-scope="scope">
                        {{scope.row.CreateTime | capitalize}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="OrderId"
                        width="180px"
                        label="订单号">
                    <template slot-scope="scope">
                        <!-- 此处传入订单Id -->
                        <router-link style="color:#409eff" :to="{path:'/ordermanage/order_detail',query:{OrderId:scope.row.Id}}" class="showOrder">
                            {{scope.row.OrderId}}
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="ProductName"
                        label="商品名">
                </el-table-column>
                <el-table-column
                        prop="Amount"
                        label="数量">
                </el-table-column>
                <el-table-column
                        prop="TotalShouldPayPrice"
                        label="总金额">
                </el-table-column>
                <el-table-column
                        prop="Discount"
                        label="优惠金额">
                </el-table-column>
                <el-table-column
                        prop="Price"
                        label="实付金额">
                </el-table-column>
                <el-table-column
                        prop="Status"
                        label="订单状态">
                    <template slot-scope="scope">
                        <span v-if="scope.row.Status == 'EXPIRED'">	已过期</span>
                        <span v-else-if="scope.row.Status == 'PRE_AFTER_SALE'">售后订单待确认</span>
                        <span v-else-if="scope.row.Status == 'UNPAID'">待确认支付</span>
                        <span v-else-if="scope.row.Status == 'CANCELED'">已取消</span>
                        <span v-else-if="scope.row.Status == 'PRESELL_PAID'">已支付定金</span>
                        <span v-else-if="scope.row.Status == 'BEGIN_FINAL_PAY'">开始支付尾款</span>
                        <span v-else-if="scope.row.Status == 'WAIT_FOR_FINAL_PAY'">待支付尾款</span>
                        <span v-else-if="scope.row.Status == 'PAID'">已完成支付</span>
                        <span v-else-if="scope.row.Status == 'UNDELIVERED'">待发货</span>
                        <span v-else-if="scope.row.Status == 'DELIVERED'">已发货</span>
                        <span v-else-if="scope.row.Status == 'COMPLETED'">已确认收货</span>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    background
                    :current-page="currentPage1"
                    @current-change="getOrderList"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="totalNo1">
            </el-pagination>
        </el-card>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>优惠券</span>
            </div>
            <el-table
                    border
                    :data="tableData2"
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="ID"
                        label="ID">
                    <template slot-scope="scope">
                        <span>{{scope.row.CouponActivity.ID}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="CreateTime"
                        label="创建时间">
                    <template slot-scope="scope">
                        <span>{{scope.row.CouponActivity.CreateTime | capitalize}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Name"
                        label="活动">
                    <template slot-scope="scope">
                        <span>{{scope.row.CouponActivity.Name}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Type"
                        label="类型">
                    <template slot-scope="scope">
                        <span v-if="scope.row.CouponActivity.Type == 1">代金券</span>
                        <span v-if="scope.row.CouponActivity.Type == 2">满减券</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Name"
                        label="优惠券名称">
                    <template slot-scope="scope">
                        <span>{{scope.row.CouponActivity.Name}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="EndTime"
                        label="有效时间">
                    <template slot-scope="scope">
                        <span>{{scope.row.CouponActivity.StartTime | capitalize}}-{{scope.row.CouponActivity.EndTime | capitalize}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Status"
                        label="状态">
                    <template slot-scope="scope">
                        <span v-if="scope.row.CouponActivity.Status == 0">失效</span>
                        <span v-else-if="scope.row.CouponActivity.Status == 1">有效</span>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    background
                    :current-page="currentPage1"
                    @current-change="getCoupon"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="totalNo2">
            </el-pagination>
        </el-card>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>优惠码</span>
            </div>
            <el-table
                    border
                    :data="tableData3"
                    max-height=400
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="Id"
                        label="ID">
                </el-table-column>
                <el-table-column
                        prop="CreateTime"
                        label="创建时间">
                    <template slot-scope="scope">
                        {{scope.row.CouponCode.CouponActivity.CreateTime | capitalize}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Name"
                        label="活动">
                    <template slot-scope="scope">
                        {{scope.row.CouponCode.CouponActivity.Name}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Type"
                        label="类型">
                    <template slot-scope="scope">
                        <span v-if="scope.row.CouponCode.CouponActivity.Type == 1">代金券</span>
                        <span v-if="scope.row.CouponCode.CouponActivity.Type == 2">满减券</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Code"
                        label="优惠券码">
                    <template slot-scope="scope">
                        {{scope.row.CouponCode.Code}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="address"
                        label="有效时间">
                    <template slot-scope="scope">
                        <span>{{scope.row.CouponCode.CouponActivity.StartTime | capitalize}}-{{scope.row.CouponCode.CouponActivity.EndTime | capitalize}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="address"
                        label="状态">
                    <template slot-scope="scope">
                        <span v-if="scope.row.CouponCode.CouponActivity.Status == 0">过期</span>
                        <span v-else-if="scope.row.CouponCode.CouponActivity.Status == 1">审核中</span>
                        <span v-else-if="scope.row.CouponCode.CouponActivity.Status == 2">审核成功</span>
                        <span v-else-if="scope.row.CouponCode.CouponActivity.Status == 3">审核失败</span>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    background
                    :current-page="currentPage1"
                    @current-change="getCouponCode"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="totalNo3">
            </el-pagination>
        </el-card>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>账户明细</span>
            </div>
            <el-table
                    border
                    :data="tableData4"
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="Time"
                        label="时间">
                    <template slot-scope="scope">
                        {{scope.row.Time | capitalize}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Business"
                        label="类型">
                    <template slot-scope="scope">
                        <span v-if="scope.row.Business ==  'balance_to_order'">余额支付</span>
                        <span v-else-if="scope.row.Business ==  'withdraw'">提现</span>
                        <span v-else-if="scope.row.Business ==  'fund_order_and_to_balance'">订单转余额</span>
                        <span v-else-if="scope.row.Business ==  'fund'">余额充值</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="Btcamount"
                        label="数量">
                    <template slot-scope="scope">
                        {{scope.row.BtcAfter - scope.row.BtcBefore}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="BtcAfter"
                        label="余额">
                </el-table-column>
                <el-table-column
                        prop="ObjectId"
                        label="交易/订单号">
                    <template slot-scope="scope">
                        <!-- 此处传入订单Id -->
                        <router-link :to="{path:'/ordermanage/order_detail',query:{OrderId:scope.row.ObjectId}}" class="showOrder">
                            {{scope.row.Id}}
                        </router-link>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    :total="totalNo4">
            </el-pagination>
        </el-card>

        <el-dialog
                title="修改邮箱地址"
                :visible.sync="dialogVisible"
                width="30%">
            <h4>用户名</h4>
            <el-input
                    :placeholder="this.Account"
                    :disabled="true">
            </el-input>
            <h4>原邮箱地址</h4>
            <el-input
                    :disabled="true"
                    :placeholder="this.Email">
            </el-input>
            <h4>新邮箱地址</h4>
            <el-input
                    placeholder="新邮箱地址"
                    v-model="amendInput1"
                    clearable>
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitNewEmail">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    export default {
        name:'',
        data(){
            return{
                dialogVisible: false,
                amendInput1:'',
                Id: 0,
                Account: '',
                Email: '',
                RegistTime: '',
                RegistIp: '',
                Source: '',
                userStatus: false,
                LoginPswdHash:'',
                Btc: 0,
                currentPage1: 1,
                totalNo1: 0,
                totalNo2: 0,
                totalNo3: 0,
                totalNo4: 0,
                EmailVerified: false,
                totp: false,
                sms: false,
                PhoneNumber: 0,
                PhonePrefix: 0,
                tableData1: [], // 购买记录
                tableData2: [], // 优惠券
                tableData3: [],
                tableData4: []
            }
        },
        methods: {
            // rebuidSms(){
            //    this.$confirm('此操作将重置短信验证, 是否继续?', '提示', {
            //         confirmButtonText: '确定',
            //         cancelButtonText: '取消',
            //         type: 'warning'
            //     }).then(() => {
            //         this.$api('/admin/user/reset_phone', {
            //             uid:this.Id
            //         }).then(res => {
            //             // console.log(this.userStatus)
            //             // console.log(this.Id)
            //             this.sms = 0
            //         });
            //     this.$message({
            //         type: 'success',
            //         message: '重置短信验证成功!'
            //     });
            //     }).catch(() => {
            //         this.$message({
            //             type: 'info',
            //             message: '已取消重置'
            //         });
            //     });
            // },
            unbundle(){
                this.$confirm('此操作将解绑用户谷歌验证, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$api('/admin/user/remove_totp', {
                        uid:this.Id,
                        notice: '本用户谷歌验证解绑成功'
                    }).then(res => {
                        // console.log(this.userStatus)
                        // console.log(res)
                        setTimeout('location.reload()', 1500)
                    });
                }).catch(() => {
                        this.$message({
                        type: 'info',
                        message: '已取消解绑'
                    });
                });
            },
            updateUserActive(val){
                this.$confirm('此操作将更改用户账号状态, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    // 封禁状态取反
                    // this.userStatus = !this.userStatus
                    this.$api('/admin/user/update_user_active', {
                        uid:this.Id,
                        active: val,
                        notice:'更改状态成功!'
                    }).then(res => {
                        // console.log(this.userStatus)
                        // console.log(res)
                        setTimeout('location.reload()', 1500)
                    });
                }).catch(() => {
                        this.$message({
                        type: 'info',
                        message: '已取消更改状态'
                    });
                });
            },
            submitNewEmail(){
                // console.log(this.amendInput1)
                this.$api('/admin/user/update_user_email', {
                    uid:this.Id,
                    email: this.amendInput1
                }).then(res => {
                    alert('邮箱修改成功')
                    this.Email = this.amendInput1
                });
                this.dialogVisible = false
            },
            vertifyEmail(){
                this.$api('/admin/user/vertify_user_email', {
                    uid:this.Id,
                    email: this.Email
                }).then(res => {
                    alert('邮件已发送至' + this.Email + '，请注意查收')
            });
            },
            getUserDetail(){
                // console.log(this.Id)
                this.$api('/admin/user/get_user_detail', {
                    uid:this.Id
                }).then(res => {
                    console.log(res)
                let d = res.data
                // this.Id = res.data.Id
                this.userStatus = !d.userInfo.Active
                this.EmailVerified = d.userInfo.EmailVerified
                this.Account = d.userInfo.Account
                this.Email = d.userInfo.Email
                this.RegistTime = new Date(parseInt(d.userInfo.RegistTime) * 1000).toISOString().slice(0,10)
                this.RegistIp = d.userInfo.RegistIp
                this.Source = d.userInfo.Source
                this.Btc = d.balance.Btc
                this.PhoneNumber = d.userInfo.PhoneNumber
                this.PhonePrefix = d.userInfo.PhonePrefix
                d.userInfo.LoginPswdHash?this.LoginPswdHash = d.userInfo.LoginPswdHash:0
                if(d.userInfo.AuthType == 'totp' && d.userInfo.AuthType != ''){
                    this.totp = true
                }else if(d.userInfo.AuthType == '' && d.userInfo.TotpAuthKey !=''){
                    this.totp = true
                }else {
                    this.totp = false
                }
                // 绑定了谷歌验证和短信验证AuthType='totp',绑定了短信验证AuthType='sms',都没绑为''
                (d.userInfo.AuthType == 'sms' || d.userInfo.AuthType == 'totp')?this.sms = d.userInfo.AuthType:0
                // console.log(this.sms)
            });
            },
            getOrderList(val){
                // 购买记录
                this.$api('/admin/order/get_user_order_list', {
                    uid:this.Id,
                    pageNo:val,
                    pageSize: 10
                }).then(res => {
                    // console.log(res)
                    this.tableData1 = res.data.orderList
                this.totalNo1 = res.data.page.totalNum
            });
            },
            getCoupon(val){
                // 优惠券
                this.$api('/admin/user/get_user_coupon_record', {
                    uid:this.Id,
                    pageNo: val,
                    pageSize: 10
                }).then(res => {
                    console.log(res)
                this.tableData2 = res.data.userCouponRecord
                this.totalNo2 = res.data.page.totalNum
            });
            },
            getCouponCode(val){
                // 优惠码
                this.$api('/admin/user/get_user_coupon_code', {
                    uid:this.Id,
                    pageNo: val,
                    pageSize: 10
                }).then(res => {
                    // console.log(res)
                    this.tableData3 = res.data.userCpnHistList
                this.totalNo3 = res.data.page.totalNum
            });
            },
            getBtcList(val){
                // 账户明细
                this.$api('/admin/user/get_user_btc_history_list', {
                    uid:this.Id,
                    pageNo:val,
                    pageSize:10
                }).then(res => {
                    // console.log(res)
                    this.tableData4 = res.data.btcHisList
                this.totalNo4 = res.data.page.totalNum
            });
            }
        },
        created(){
            // console.log(this.$route.query.uid)
            this.Id = this.$route.query.uid
            this.getUserDetail()
            this.getOrderList(1)
            this.getCoupon(1)
            this.getCouponCode(1)
            this.getBtcList(1)
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
    .usermessage{
        width: 100%
    }
    .usermessage td{
        border: 1px solid #ebeef5
    }
    .usermessage tr td:first-child{
        width: 15%;
        height: 40px;
        font-size: 14px;
        font-weight: 900;
        padding-left: 30px
    }
    .usermessage tr td:nth-child(2){
        width: 85%;
        font-size: 14px;
        padding-left: 30px;
    }
    .showOrder:hover{
        color: #409EFF;
        text-decoration: underline
    }
    .activeCls{
        background-color: #26507B;
        color: #ffffff
    }
</style>


